import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {
  MatButtonModule,
  MatDialogModule,
  MatFormFieldModule,
  MatIconModule,
  MatRippleModule,
  MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';

import { ErrorComponent } from './error/error.component';
import { MediaDialogComponent } from './media-dialog/media-dialog.component';

@NgModule({
  imports: [
    CommonModule,
  
    FuseSharedModule,
    MatButtonModule,
    MatDialogModule,
    MatFormFieldModule,
    MatIconModule,
    MatRippleModule,
    MatToolbarModule
  ],
  declarations: [ErrorComponent, MediaDialogComponent],
  exports: [ErrorComponent, MediaDialogComponent],
  entryComponents: [MediaDialogComponent]
})
export class SharedModule { }



